import { useJsonForms } from '@jsonforms/react';
import React from 'react';
import { FormSet } from '@dnb/eufemia/components';
import { P } from '@dnb/eufemia/elements';
import { RendererProps } from '@jsonforms/core';
import classNames from 'classnames';
import { Stack } from '@pm-netbank/components';
import { translateText } from '../../i18n';
import { ContainerLayout } from '../types';
import { renderChildren } from '../utils';
import styles from './ContainerLayout.module.scss';

const ContainerLayoutRenderer = ({
  schema,
  uischema,
  enabled,
  path,
  visible,
  renderers,
}: RendererProps) => {
  const jsonFormsContext = useJsonForms();

  if (!visible) return null;

  const layout = uischema as ContainerLayout;

  const hasGroupAsChildren = (layout.elements || []).some(
    (e) => e.type === 'Group'
  );

  const classes = classNames(
    styles.container,
    hasGroupAsChildren && styles.hasGroup
  );

  return (
    <>
      {getLabel()}
      {getContent()}
    </>
  );

  function getLabel() {
    if (!layout.i18n && !layout.label) return null;

    return (
      <P
        className="dnb-typo-medium"
        bottom="small"
        data-testid="container-title"
      >
        {translateText(jsonFormsContext.i18n, layout.i18n, layout.label)}
      </P>
    );
  }

  function getContent() {
    switch (layout.options?.containerType) {
      case 'form':
        return (
          <FormSet direction="vertical" className={classes}>
            {renderChildren(layout, schema, path, enabled, renderers)}
          </FormSet>
        );
      case 'stack':
        return (
          <Stack>
            {renderChildren(layout, schema, path, enabled, renderers)}
          </Stack>
        );
      default:
        return (
          <div className={classes}>
            {renderChildren(layout, schema, path, enabled, renderers)}
          </div>
        );
    }
  }
};

export default React.memo(ContainerLayoutRenderer);
