import React from 'react';
import { ControlProps } from '@jsonforms/core';
import { SimpleCell } from '@pm-netbank/components';

const ControlSummaryRenderer = (props: ControlProps) => {
  const { visible, label, data, path } = props;

  if (!visible) return null;

  return (
    <SimpleCell
      data-testid={path}
      title={label}
      secondaryText={normalizeDataValue(data)}
    />
  );
};

export default ControlSummaryRenderer;

function normalizeDataValue(value: any) {
  switch (typeof value) {
    case 'boolean':
      return value ? 'Ja' : 'Nei';
    default:
      return value;
  }
}
