import React from 'react';
import { FormattedMessage } from 'react-intl';
import { Div, Dropdown, H2, H3, P, Skeleton, Space } from '@dnb/eufemia';
import { translationKeys } from '@pm-netbank/translations';
import withSpacing from '../helpers/withSpacing';
import styles from './TitledSection.module.scss';

export enum HeadingTypes {
  H2 = 'H2',
  H3 = 'H3',
}

interface OptionsProps {
  icon?: React.ReactNode;
  text: string | React.ReactNode;
  onSelect?: () => void;
}

export interface Props {
  title?: string | React.ReactNode;
  subtitle?: string | React.ReactNode;
  isSkeleton?: boolean;
  options?: OptionsProps[];
  optionsLabel?: string | React.ReactNode;
  className?: string;
  children: React.ReactNode;
  'data-testid'?: string;
  titleElement?: HeadingTypes;
}

const TitledSection = ({
  title,
  subtitle,
  options,
  optionsLabel,
  isSkeleton,
  className,
  children,
  titleElement = HeadingTypes.H2,
  'data-testid': dataTestid,
}: Props) => {
  function onSelectOption({ active_item }: { active_item: number }) {
    if (!options[active_item]?.onSelect) return;

    options[active_item]?.onSelect();
  }

  const TitleElement = titleElement === HeadingTypes.H3 ? H3 : H2;

  return (
    <section className={className} data-testid={dataTestid || 'titled-section'}>
      <div className={styles.headerContainer}>
        <Skeleton
          show={isSkeleton}
          data-testid={`titled-section-skeleton-${
            isSkeleton ? 'shown' : 'hidden'
          }`}
        >
          <TitleElement className="dnb-lead" data-testid="titled-section-title">
            {getTitle()}
          </TitleElement>
          {getSubtitle()}
        </Skeleton>
        {getOptions()}
      </div>
      <Space bottom="small" />
      {children}
    </section>
  );

  function getTitle() {
    if (isSkeleton)
      return (
        <FormattedMessage
          id={translationKeys.components.components_skeleton_loading_text}
        />
      );

    if (!title) return null;

    return title;
  }

  function getSubtitle() {
    if (!subtitle) return null;

    return (
      <P data-testid="titled-section-subtitle" top="xx-small">
        {subtitle}
      </P>
    );
  }

  function getOptions() {
    if (!Array.isArray(options) || options.length === 0) return null;

    return (
      <Dropdown
        data-testid="titled-section-options"
        more_menu="true"
        size="large"
        align_dropdown="right"
        data={options.map(mapOptionToDropdownData)}
        on_select={onSelectOption}
        label={getOptionsLabel()}
        variant="tertiary"
      />
    );
  }

  function getOptionsLabel() {
    return optionsLabel ? (
      <span data-testid="titled-section-options-label">{optionsLabel}</span>
    ) : null;
  }
};

function mapOptionToDropdownData({ icon, text }: OptionsProps) {
  return (
    <Div className={styles.option} data-testid="titled-section-option">
      {icon}
      <Space right="x-small" />
      {text}
    </Div>
  );
}

export default withSpacing(TitledSection);
