import classnames from 'classnames';
import _clamp from 'lodash/clamp';
import { useSprings, animated, to } from '@react-spring/web';
import { useDrag } from '@use-gesture/react';
import React from 'react';
import { Span } from '@dnb/eufemia';
import styles from './ReorderableStack.module.scss';

export interface Props {
  visible: React.ReactNode[];
  hidden: React.ReactNode[];
  onOrderChange: (accounts: { visible: string[]; hidden: string[] }) => void;
  breakText: string | React.ReactNode;
  'data-testid'?: string;
}

const ITEM_HEIGHT = 80;
const STICKY_HEIGHT = 20;
const BREAK_ITEM = 'BREAK_ITEM';
const ANIMATION_DURATION = 200;

function ReorderableStack({
  visible,
  hidden,
  onOrderChange,
  breakText,
  'data-testid': dataTestid,
}: Props) {
  const allVisible = React.Children.toArray(visible);
  const allHidden = React.Children.toArray(hidden);

  const testId = dataTestid || 'stack';

  const items = allVisible.concat([BREAK_ITEM]).concat(allHidden);
  const order = items.map((_, index) => index);
  const [springs, setSprings] = useSprings(
    items.length,
    calculateStyles(order)
  );

  let totalMovement = 0;
  let oldOrder = items.map((_, index) => index);

  const bind = useDrag(
    ({ args: [originalIndex], active, delta: [, dy] }) => {
      if (items[originalIndex] === BREAK_ITEM) return;

      totalMovement += dy;
      if (totalMovement === 0) return;

      const oldIndex = oldOrder.indexOf(originalIndex);
      const currentIndex = order.indexOf(originalIndex);
      const rowsMovement =
        totalMovement > 0
          ? Math.floor((totalMovement + STICKY_HEIGHT) / ITEM_HEIGHT)
          : Math.ceil((totalMovement - STICKY_HEIGHT) / ITEM_HEIGHT);

      const newIndex = _clamp(oldIndex + rowsMovement, 0, items.length - 1);

      totalMovement = _clamp(
        totalMovement,
        oldIndex * ITEM_HEIGHT * -1,
        (items.length - 1 - oldIndex) * ITEM_HEIGHT
      );

      const newOrder = swap(order, currentIndex, newIndex);

      setSprings(
        calculateStyles(
          newOrder,
          oldOrder,
          active,
          originalIndex,
          totalMovement
        )
      );

      if (!active) {
        totalMovement = 0;
        oldOrder = newOrder.slice();

        const originalBreakIndex = items.indexOf(BREAK_ITEM);
        const currentBreakIndex = newOrder.indexOf(originalBreakIndex);
        const visibleIndices = newOrder.slice(0, currentBreakIndex);
        const hiddenIndices = newOrder.slice(currentBreakIndex + 1);

        const visibleAccounts = visibleIndices.map(
          (idx: number) =>
            (items[idx] as React.ReactElement).props.account.accountNumber
        );
        const hiddenAccounts = hiddenIndices.map(
          (idx: number) =>
            (items[idx] as React.ReactElement).props.account.accountNumber
        );

        setTimeout(() => {
          onOrderChange({ visible: visibleAccounts, hidden: hiddenAccounts });
        }, ANIMATION_DURATION);
      }
    },
    { delay: 250, preventScroll: true }
  );

  return (
    <div
      data-testid={testId}
      className={styles.reorderableStack}
      style={{ height: items.length * ITEM_HEIGHT + 2 }}
    >
      {springs.map(({ active, zIndex, shadow, y, scale, borderRadius }, i) => {
        if (items[i] === BREAK_ITEM) {
          return (
            <animated.div
              {...bind(i)}
              className={styles.breakLine}
              key={`${items[i]} - ${i}`}
              style={{
                zIndex,
                transform: to(
                  [y, scale],
                  (y, scale) => `translate3d(0,${y}px,0) scale(${scale})`
                ),
                borderRadius,
              }}
            >
              <Span className={styles.breakText}>{breakText}</Span>
            </animated.div>
          );
        }

        const isLastVisible = i === allVisible.length - 1;
        return (
          <animated.div
            key={i}
            {...bind(i)}
            className={classnames(styles.item, {
              [styles.noBorder]: isLastVisible,
            })}
            style={{
              touchAction: active ? 'none' : 'auto',
              zIndex,
              boxShadow: shadow.to(
                (shadowValue) =>
                  `rgba(0, 0, 0, 0.15) 0px ${shadowValue}px ${
                    2 * shadowValue
                  }px 0px`
              ),
              borderRadius,
              transform: to(
                [y, scale],
                (y, scale) => `translate3d(0,${y}px,0) scale(${scale})`
              ),
            }}
          >
            {items[i]}
          </animated.div>
        );
      })}
    </div>
  );
}

function calculateStyles(
  order: number[],
  oldOrder?: number[],
  active?: boolean,
  originalIndex?: number,
  y?: number
) {
  return (index: number) => {
    if (active && index === originalIndex) {
      return {
        y: oldOrder.indexOf(originalIndex) * ITEM_HEIGHT + y,
        scale: 1.03,
        active,
        zIndex: 1,
        shadow: 15,
        borderRadius: calculateBorderRadius(originalIndex, order),
        immediate: (property: string) =>
          property === 'y' ||
          property === 'zIndex' ||
          property === 'borderRadius',
        config: { duration: ANIMATION_DURATION },
      };
    }

    return {
      y: order.indexOf(index) * ITEM_HEIGHT,
      scale: 1,
      active,
      zIndex: 0,
      shadow: 0,
      borderRadius: calculateBorderRadius(index, order),
      immediate: false,
      config: { duration: ANIMATION_DURATION },
    };
  };
}

function calculateBorderRadius(index: number, order: number[]) {
  const isFirstItem = order.indexOf(index) === 0;
  if (isFirstItem) {
    return '0.25rem 0.25rem 0 0';
  }

  const isLastItem = order.indexOf(index) === order.length - 1;
  if (isLastItem) {
    return '0 0 0.25rem 0.25rem';
  }

  return '0 0 0 0';
}

const swap = (array: number[], from: number, to: number) => {
  const item = array[from];
  array[from] = array[to];
  array[to] = item;
  return array;
};

export default ReorderableStack;
