import React from 'react';
import { Div, FormRow, Button, Icon, Autocomplete, Input } from '@dnb/eufemia';
import { add_circled, trash } from '@dnb/eufemia/icons';
import { ControlProps } from '@jsonforms/core';
import styles from './TaxCountryListControl.module.scss';

interface TaxCountryListControlProps extends ControlProps {
  data: any;
  handleChange(path: string, value: any): void;
  path: string;
}

const TaxCountryList = (props: TaxCountryListControlProps) => {
  console.log('TaxCountryList');
  const [countries, setCountries] = React.useState([
    { name: 'country', disabled: false, tnid: '' },
  ]);

  const addCountry = (country) => {
    const newCountries = [...countries, country];
    setCountries(newCountries);
  };

  const disableCountry = (index, disabled) => {
    console.log(disabled);
    const newCountries = [...countries];
    newCountries[index].tnid = '';
    newCountries[index].disabled = !disabled;
    setCountries(newCountries);
  };

  const setTnid = (index, tnid) => {
    const newCountries = [...countries];
    newCountries[index].tnid = tnid;
    setCountries(newCountries);
  };

  const removeCountry = (index) => {
    const newCountries = [...countries];
    newCountries.splice(index, 1);
    setCountries(newCountries);
  };

  const { uischema, handleChange, path, visible, schema, addItem } = props;

  if (!visible) {
    return null;
  }

  const dataSorted = countryMap.sort((a, b) => a.localeCompare(b));

  return (
    <Div className={uischema?.options?.border && styles.top}>
      {countries.map((country, index) => (
        <FormRow
          direction="horizontal"
          key={index}
          top="x-small"
          bottom="small"
          className={styles.border}
        >
          <Autocomplete
            size="medium"
            right="medium"
            stretch
            placeholder={'Velg land'}
            data={dataSorted}
            label_direction="vertical"
          />
          <Input
            right="medium"
            stretch
            size="medium"
            bottom="small"
            placeholder={'TIN/ID-nummer'}
            label_direction="vertical"
            disabled={country.disabled}
            value={country.tnid}
            on_change={({ value }) => {
              setTnid(index, value);
            }}
          />
          <Button
            className={styles.button}
            right="medium"
            text="Har ikke/husker ikke"
            variant="tertiary"
            on_click={() => {
              disableCountry(index, country.disabled);
            }}
          />
          <Button
            title="Slett"
            on_click={() => {
              removeCountry(index);
            }}
            icon={<Icon icon={trash} alt="Slett" title="Slett" />}
          />
        </FormRow>
      ))}
      <Button
        top="small"
        bottom="small"
        icon_position="left"
        text="Velg et land til"
        variant="tertiary"
        icon={<Icon icon={add_circled} />}
        on_click={() => {
          addCountry({ name: 'country', disabled: false, tnid: '' });
          addItem(path, 'test');
        }}
      />
    </Div>
  );
};
export default TaxCountryList;

