import { Money } from '@pm-netbank/types';
import React from 'react';
import { NumberFormat } from '@dnb/eufemia';

export interface Props {
  money: Money;
  className?: string;
  selectall?: boolean;
  decimals?: string;
}

const FormattedMoney = ({ money, className, selectall, decimals }: Props) => {
  return (
    <NumberFormat
      data-testid="formatted-money"
      currency={money.currency}
      value={money.value}
      currency_display="symbol"
      className={className}
      selectall={selectall}
      decimals={decimals}
    />
  );
};

export default FormattedMoney;
