import { getLang } from '@dnb/locale';

interface epkFlexRoutes {
  base: () => string;
  eligibility: () => string;
  questionnaire: () => string;
  ageQuestion: () => string;
  riskQuestion: () => string;
  strategySuggestion: () => string;
  investmentOptions: () => string;
  terms: () => string;
  filterFundTypes: () => string;
  filterFundRegions: () => string;
  filterFundRisk: () => string;
  funds: () => string;
  fundsDistribution: () => string;
  fundsSummary: () => string;
  keyInformation: () => string;
  complete: () => string;
  filterProfileTypes: () => string;
  profiles: () => string;
  profileDownWeighting: () => string;
  profilesSummary: () => string;
  confirmChoice: () => string;
  onboardingResult: () => string;
}

const enBasePath = '/epk-flex';

const enRoutes: epkFlexRoutes = {
  base: () => enBasePath,
  eligibility: () => `${enBasePath}/eligibility`,
  questionnaire: () => `${enBasePath}/questionnaire`,
  ageQuestion: () => `${enBasePath}/questionnaire/age`,
  riskQuestion: () => `${enBasePath}/questionnaire/risk`,
  strategySuggestion: () => `${enBasePath}/questionnaire/strategy`,
  investmentOptions: () => `${enBasePath}/investment-options`,
  terms: () => `${enBasePath}/terms`,
  filterFundTypes: () => `${enBasePath}/funds/filter-types`,
  filterFundRegions: () => `${enBasePath}/funds/filter-regions`,
  filterFundRisk: () => `${enBasePath}/funds/filter-risk`,
  funds: () => `${enBasePath}/funds`,
  fundsDistribution: () => `${enBasePath}/funds/distribution`,
  fundsSummary: () => `${enBasePath}/funds/summary`,
  keyInformation: () => `${enBasePath}/key-information`,
  complete: () => `${enBasePath}/complete`,
  filterProfileTypes: () => `${enBasePath}/profiles/filter-types`,
  profiles: () => `${enBasePath}/profiles`,
  profileDownWeighting: () => `${enBasePath}/profiles/down-weighting`,
  profilesSummary: () => `${enBasePath}/profiles/summary`,
  confirmChoice: () => `${enBasePath}/confirmation/choice`,
  onboardingResult: () => `${enBasePath}/confirmation/result`,
};

const noBasePath = '/epk-flex';

const noRoutes: epkFlexRoutes = {
  base: () => noBasePath,
  eligibility: () => `${enBasePath}/eligibility`,
  questionnaire: () => `${enBasePath}/questionnaire`,
  ageQuestion: () => `${enBasePath}/questionnaire/age`,
  riskQuestion: () => `${enBasePath}/questionnaire/risk`,
  strategySuggestion: () => `${enBasePath}/questionnaire/strategy`,
  investmentOptions: () => `${enBasePath}/investment-options`,
  terms: () => `${enBasePath}/terms`,
  filterFundTypes: () => `${enBasePath}/funds/filter-types`,
  filterFundRegions: () => `${enBasePath}/funds/filter-regions`,
  filterFundRisk: () => `${enBasePath}/funds/filter-risk`,
  funds: () => `${enBasePath}/funds`,
  fundsDistribution: () => `${enBasePath}/funds/distribution`,
  fundsSummary: () => `${enBasePath}/funds/summary`,
  complete: () => `${enBasePath}/complete`,
  filterProfileTypes: () => `${enBasePath}/profiles/filter-types`,
  profiles: () => `${enBasePath}/profiles`,
  profileDownWeighting: () => `${enBasePath}/profiles/down-weighting`,
  profilesSummary: () => `${enBasePath}/profiles/summary`,
  keyInformation: () => `${enBasePath}/key-information`,
  confirmChoice: () => `${enBasePath}/confirmation/choice`,
  onboardingResult: () => `${enBasePath}/confirmation/result`,
};

function getRoutes(): epkFlexRoutes {
  if (getLang() === 'nb-NO') return noRoutes;
  return enRoutes;
}

const routes = getRoutes();

export default routes;
