import { getRoutes } from './helpers';

interface FundsRoutes {
  base: () => string;
  overview: () => string;
  fundDetails: (isin: string) => string;
  fundAccountsWithHoldings: () => string;
  fundAccountDetails: (accountNumber: string) => string;
  fundTransactions: () => string;
  fundTransactionDetails: (transactionId: string) => string;
  fundTransactionsOrderDetails: (orderId: string) => string;
  fundOrderDetails: (orderId: string) => string;
  savingsAgreements: () => string;
  savingsAgreementDetails: (savingsAgreementId: string) => string;
  browse: () => string;
}

const enBasePath = '/funds';
const enRoutes: FundsRoutes = {
  base: () => enBasePath,
  overview: () => `${enBasePath}/overview`,
  fundDetails: (isin: string) => `${enBasePath}/${isin}`,
  fundAccountsWithHoldings: () => `${enBasePath}/fund-accounts`,
  fundAccountDetails: (accountNumber: string) =>
    `${enBasePath}/fund-accounts/${accountNumber}`,
  fundTransactions: () => `${enBasePath}/fund-transactions`,
  fundTransactionDetails: (transactionId) =>
    `${enBasePath}/fund-transactions/${transactionId}`,
  fundTransactionsOrderDetails: (orderId) =>
    `${enBasePath}/fund-transactions/fund-orders/${orderId}`,
  fundOrderDetails: (orderId) =>
    `${enRoutes.overview()}/fund-orders/${orderId}`,
  savingsAgreements: () => `${enBasePath}/savings-agreements`,
  savingsAgreementDetails: (savingsAgreementId: string) =>
    `${enBasePath}/savings-agreements/${savingsAgreementId}`,
  browse: () => `${enBasePath}/browse`,
};

const noBasePath = '/funds';
const noRoutes: FundsRoutes = {
  base: () => noBasePath,
  overview: () => `${noBasePath}/overview`,
  fundDetails: (isin: string) => `${noBasePath}/${isin}`,
  fundAccountsWithHoldings: () => `${noBasePath}/fund-accounts`,
  fundAccountDetails: (accountNumber: string) =>
    `${noBasePath}/fund-accounts/${accountNumber}`,
  fundTransactions: () => `${noBasePath}/fund-transactions`,
  fundTransactionDetails: (transactionId) =>
    `${noBasePath}/fund-transactions/${transactionId}`,
  fundTransactionsOrderDetails: (orderId) =>
    `${noBasePath}/fund-transactions/fund-orders/${orderId}`,
  fundOrderDetails: (orderId) =>
    `${noRoutes.overview()}/fund-orders/${orderId}`,
  savingsAgreements: () => `${noBasePath}/savings-agreements`,
  savingsAgreementDetails: (savingsAgreementId: string) =>
    `${noBasePath}/savings-agreements/${savingsAgreementId}`,
  browse: () => `${noBasePath}/browse`,
};

export default getRoutes(enRoutes, noRoutes);
