import { getRoutes } from './helpers';

interface PaymentAgreementsRoutes {
  base: () => string;
  paymentAgreementTypeDetails: (agreementType: string) => string;
  paymentAgreementDetails: (agreementType: string, id: string) => string;
  paymentAgreementHistory: (agreementType: string, id: string) => string;
  paymentAddNewAgreement: (agreementType: string) => string;
  paymentAddNewAgreementFromProposal: (
    agreementType: string,
    proposalObjectId: string
  ) => string;
  eInvoiceIssuerDetails: (issuerId: string) => string;
  paymentAgreementInfo: (agreementType: string) => string;
  eInvoiceInfo: () => string;
  eInvoiceBlockedIssuers: () => string;
  forwardEInvoices: () => string;
  eInvoiceAgreementsMine: () => string;
  eInvoiceAgreementsForwarded: () => string;
  eInvoiceReceivedForwardDetails: (id: string) => string;
  eInvoiceReceivedForwardBlockedIssuers: (id: string) => string;
  eInvoiceReceivedForwardIssuerDetails: (
    forwardId: string,
    issuerId: string
  ) => string;
  eInvoiceMyInfo: () => string;
}

const enBasePath = '/payment-agreements';
const enRoutes: PaymentAgreementsRoutes = {
  base: () => enBasePath,
  paymentAgreementTypeDetails: (agreementType) =>
    `${enBasePath}/${agreementType}`,
  paymentAgreementDetails: (agreementType, id) =>
    `${enBasePath}/${agreementType}/${id}/details`,
  paymentAgreementHistory: (agreementType, id) =>
    `${enBasePath}/${agreementType}/${id}/history`,
  paymentAddNewAgreement: (agreementType) =>
    `${enBasePath}/${agreementType}/add`,
  eInvoiceIssuerDetails: (issuerId) =>
    `${enBasePath}/einvoice/mine/issuers/${issuerId}`,
  paymentAddNewAgreementFromProposal: (agreementType, proposalObjectId) =>
    `${enBasePath}/${agreementType}/add/${proposalObjectId}`,
  paymentAgreementInfo: (agreementType) =>
    `${enBasePath}/${agreementType}/info`,
  eInvoiceInfo: () => `${enBasePath}/einvoice/mine/info`,
  eInvoiceBlockedIssuers: () => `${enBasePath}/einvoice/mine/blocked-issuers`,
  forwardEInvoices: () => `${enBasePath}/einvoice/mine/forward-invoices`,
  eInvoiceAgreementsMine: () => `${enBasePath}/einvoice/mine`,
  eInvoiceAgreementsForwarded: () => `${enBasePath}/einvoice/forwarded`,
  eInvoiceReceivedForwardDetails: (id) =>
    `${enBasePath}/einvoice/forwarded/${id}`,
  eInvoiceReceivedForwardBlockedIssuers: (id) =>
    `${enBasePath}/einvoice/forwarded/${id}/blocked-issuers`,
  eInvoiceReceivedForwardIssuerDetails: (forwardId, issuerId) =>
    `${enBasePath}/einvoice/forwarded/${forwardId}/issuers/${issuerId}`,
  eInvoiceMyInfo: () => `${enBasePath}/einvoice/mine/my-info`,
};

const noBasePath = '/payment-agreements';
const noRoutes: PaymentAgreementsRoutes = {
  base: () => noBasePath,
  paymentAgreementTypeDetails: (agreementType) =>
    `${noBasePath}/${agreementType}`,
  paymentAgreementDetails: (agreementType, id) =>
    `${noBasePath}/${agreementType}/${id}/details`,
  paymentAgreementHistory: (agreementType, id) =>
    `${noBasePath}/${agreementType}/${id}/history`,
  paymentAddNewAgreement: (agreementType) =>
    `${noBasePath}/${agreementType}/add`,
  eInvoiceIssuerDetails: (issuerId) =>
    `${noBasePath}/einvoice/mine/issuers/${issuerId}`,
  paymentAddNewAgreementFromProposal: (agreementType, proposalObjectId) =>
    `${noBasePath}/${agreementType}/add/${proposalObjectId}`,
  paymentAgreementInfo: (agreementType) =>
    `${noBasePath}/${agreementType}/info`,
  eInvoiceInfo: () => `${noBasePath}/einvoice/mine/info`,
  eInvoiceBlockedIssuers: () => `${noBasePath}/einvoice/mine/blocked-issuers`,
  forwardEInvoices: () => `${noBasePath}/einvoice/mine/forward-invoices`,
  eInvoiceAgreementsMine: () => `${noBasePath}/einvoice/mine`,
  eInvoiceAgreementsForwarded: () => `${noBasePath}/einvoice/forwarded`,
  eInvoiceReceivedForwardDetails: (id) =>
    `${noBasePath}/einvoice/forwarded/${id}`,
  eInvoiceReceivedForwardBlockedIssuers: (id) =>
    `${noBasePath}/einvoice/forwarded/${id}/blocked-issuers`,
  eInvoiceReceivedForwardIssuerDetails: (forwardId, issuerId) =>
    `${noBasePath}/einvoice/forwarded/${forwardId}/issuers/${issuerId}`,
  eInvoiceMyInfo: () => `${noBasePath}/einvoice/mine/my-info`,
};

export default getRoutes(enRoutes, noRoutes);
