import { getLang } from '@dnb/locale';

interface CardRoute {
  base: () => string;
  myCards: () => string;
  otherCards: () => string;
  blockCards: () => string;
  blockMyCards: () => string;
  blockOtherCards: () => string;
  cardActionBlock: (cardType: string, cardId: string) => string;
  cardActionSettings: (cardType: string, cardId: string) => string;
  cardActionReplace: (cardType: string, cardId: string) => string;
  cardInfoDetails: (cardType: string, cardId: string) => string;
  replaceBlockedCard: (cardType: string, cardId: string) => string;
}

const enBasePath = '/cards';
const enRoutes: CardRoute = {
  base: () => enBasePath,
  myCards: () => `${enBasePath}/mine`,
  otherCards: () => `${enBasePath}/other`,
  blockCards: () => `${enBasePath}/block`,
  blockMyCards: () => `${enBasePath}/block/mine`,
  blockOtherCards: () => `${enBasePath}/block/other`,
  cardActionBlock: (cardType: string, cardId: string) =>
    `${enBasePath}/${cardType}/${cardId}/block`,
  cardActionSettings: (cardType: string, cardId: string) =>
    `${enBasePath}/${cardType}/${cardId}/settings`,
  cardActionReplace: (cardType: string, cardId: string) =>
    `${enBasePath}/${cardType}/${cardId}/replace`,
  cardInfoDetails: (cardType: string, cardId: string) =>
    `${enBasePath}/${cardType}/${cardId}`,
  replaceBlockedCard: (cardType: string, cardId: string) =>
    `${enBasePath}/block/${cardType}/${cardId}/replace`,
};

const noBasePath = '/cards';
const noRoutes: CardRoute = {
  base: () => noBasePath,
  myCards: () => `${noBasePath}/mine`,
  otherCards: () => `${noBasePath}/other`,
  blockCards: () => `${noBasePath}/block`,
  blockMyCards: () => `${noBasePath}/block/mine`,
  blockOtherCards: () => `${noBasePath}/block/other`,
  cardActionBlock: (cardType: string, cardId: string) =>
    `${noBasePath}/${cardType}/${cardId}/block`,
  cardActionSettings: (cardType: string, cardId: string) =>
    `${noBasePath}/${cardType}/${cardId}/settings`,
  cardActionReplace: (cardType: string, cardId: string) =>
    `${noBasePath}/${cardType}/${cardId}/replace`,
  cardInfoDetails: (cardType: string, cardId: string) =>
    `${noBasePath}/${cardType}/${cardId}`,
  replaceBlockedCard: (cardType: string, cardId: string) =>
    `${noBasePath}/block/${cardType}/${cardId}/replace`,
};

function getRoutes(): CardRoute {
  if (getLang() === 'nb-NO') return noRoutes;

  return enRoutes;
}

const routes = getRoutes();

export default routes;
