import React from 'react';
import { useIntl } from 'react-intl';
import { Button, Div, H1, P } from '@dnb/eufemia';
import { undo as UndoIcon } from '@dnb/eufemia/icons';
import { PageLayout } from '@pm-netbank/page-layout';
import { translationKeys } from '@pm-netbank/translations';
import Option from './FilterDrawer.Option';
import styles from './FilterDrawer.module.scss';
import {
  DrawerOptionType,
  findOptionComponentByName,
} from './FilterDrawer.utils';

export interface Props {
  children?: React.ReactNode;
  activeOptionName?: string;
  onResetButtonClick?: () => void;
  onBackButtonClick?: () => void;
  onDrawerClose?: () => void;
}

const FilterDrawer = ({
  children,
  activeOptionName,
  onBackButtonClick,
  onDrawerClose,
  onResetButtonClick,
}: Props) => {
  const intl = useIntl();
  const childrenArray = React.Children.toArray(children);
  const option =
    activeOptionName &&
    findOptionComponentByName(childrenArray, activeOptionName);

  return (
    <PageLayout onDrawerClose={onDrawerClose}>
      <PageLayout.Breadcrumb>{getBreadcrumb()}</PageLayout.Breadcrumb>
      <PageLayout.Header>{getHeader()}</PageLayout.Header>
      <PageLayout.Content>{getContent()}</PageLayout.Content>
    </PageLayout>
  );

  function getHeader() {
    const mainTitle = intl.formatMessage({
      id: translationKeys.components.components_filter_drawer_title,
    });
    const optionTitle = option?.props?.title;
    const subTitle = option?.props?.subTitle;

    return (
      <Div bottom="large">
        <Div className={styles.headerWrapper}>
          <H1 size="x-large" data-testid="filter-drawer-title">
            {optionTitle || mainTitle}
          </H1>
          {getResetButton()}
        </Div>

        {subTitle && (
          <P
            size="small"
            className={styles.subTitle}
            top="small"
            data-testid="filter-drawer-subtitle"
          >
            {subTitle}
          </P>
        )}
      </Div>
    );
  }

  function getContent() {
    if (activeOptionName) {
      if (option) {
        return option.props.controller;
      } else {
        return (
          <P data-testid="filter-drawer-option-not-found">
            Controller related to "{activeOptionName}" (activeOptionName) not
            found.
          </P>
        );
      }
    } else {
      return (
        <Div top="small" className={styles.filterWrapper}>
          {children}
        </Div>
      );
    }
  }

  function getBreadcrumb() {
    if (!activeOptionName) {
      return null;
    }

    return (
      <Button
        icon="chevron_left"
        icon_position="left"
        variant="tertiary"
        text={intl.formatMessage({
          id: translationKeys.components
            .components_filter_drawer_back_button_label,
        })}
        onClick={onBackButtonClick}
        data-testid="filter-drawer-back-button"
      />
    );
  }

  function getResetButton() {
    if (!onResetButtonClick || activeOptionName) {
      return null;
    }

    return (
      <Button
        icon={UndoIcon}
        icon_position="left"
        variant="tertiary"
        text={intl.formatMessage({
          id: translationKeys.components
            .components_filter_drawer_reset_button_label,
        })}
        onClick={onResetButtonClick}
        data-testid="filter-drawer-reset-button"
      />
    );
  }
};

FilterDrawer.Option = (props: DrawerOptionType) => <Option {...props} />;

export default FilterDrawer;
