import { getRoutes } from './helpers';

interface AccountRoute {
  base: () => string;
  account: (accountNumber: string) => string;
  accountDetails: (accountNumber: string) => string;
  accountRoles: (accountNumber: string) => string;
  accountCardDetails: (accountNumber: string, cardId: string) => string;
  accountTransactionDetails: (
    accountNumber: string,
    transactionId: string
  ) => string;
  openAccount: () => string;
}

const enBasePath = '/accounts';
const enRoutes: AccountRoute = {
  base: () => enBasePath,
  account: (accountNumber: string) => `${enBasePath}/${accountNumber}`,
  accountDetails: (accountNumber: string) =>
    `${enBasePath}/${accountNumber}/details`,
  accountRoles: (accountNumber: string) =>
    `${enBasePath}/${accountNumber}/roles`,
  accountCardDetails: (accountNumber: string, cardId: string) =>
    `${enBasePath}/${accountNumber}/cards/${cardId}`,
  accountTransactionDetails: (accountNumber: string, transactionId: string) =>
    `${enBasePath}/${accountNumber}/transactions/${transactionId}`,
  openAccount: () => `${enBasePath}/open`,
};

const noBasePath = '/accounts';
const noRoutes: AccountRoute = {
  base: () => noBasePath,
  account: (accountNumber: string) => `${noBasePath}/${accountNumber}`,
  accountDetails: (accountNumber: string) =>
    `${noBasePath}/${accountNumber}/details`,
  accountRoles: (accountNumber: string) =>
    `${enBasePath}/${accountNumber}/roles`,
  accountCardDetails: (accountNumber: string, cardId: string) =>
    `${noBasePath}/${accountNumber}/cards/${cardId}`,
  accountTransactionDetails: (accountNumber: string, transactionId: string) =>
    `${noBasePath}/${accountNumber}/transactions/${transactionId}`,
  openAccount: () => `${noBasePath}/open`,
};

export default getRoutes(enRoutes, noRoutes);
