import React, { useState } from 'react';
import { useSpring, animated, useTransition } from 'react-spring';
import { Div, IconPrimary, P } from '@dnb/eufemia';
import classNames from 'classnames';
import { useMobile } from '@pm-netbank/hooks';
import styles from './ExpandableCardStack.module.scss';

export interface Props {
  children: React.ReactNode;
  buttonContentExpanded: React.ReactNode;
  buttonContentClosed: React.ReactNode;
  itemsPerColumn: number;
  stretchOnMobile?: boolean;
}

interface InnerComponent {
  children: React.ReactNode;
  itemsPerColumn: number;
}

const ExpandableCardStack = ({
  children,
  buttonContentExpanded,
  buttonContentClosed,
  itemsPerColumn,
  stretchOnMobile = false,
}: Props) => {
  const childrenArray = React.Children.toArray(children);

  const isMobile = useMobile();

  const numberOfItemsPerColumn =
    isMobile || childrenArray.length === 1 ? 1 : itemsPerColumn;

  const visible = (
    <StackRows itemsPerColumn={numberOfItemsPerColumn}>
      {getVisibleChildren()}
    </StackRows>
  );
  const hidden = (
    <StackRows itemsPerColumn={numberOfItemsPerColumn}>
      {getHiddenChildren()}
    </StackRows>
  );

  const [isExpanded, setIsExpanded] = useState(false);
  const expandChevronAnimations = useSpring(
    getExpandChevronAnimation(isExpanded)
  );

  const expandCellTransition = useTransition(
    isExpanded ? hidden : [],
    getCellAnimation()
  );

  return (
    <Div className={styles.stack} data-testid="expandable-card-stack">
      <Div
        data-testid="expandable-card-stack-container"
        className={classNames(styles.expandableStack, {
          [styles.stretchOnMobile]: stretchOnMobile,
        })}
      >
        {visible}
        {
          expandCellTransition((props, item) => {
            return (
              <animated.div data-testid="expand-animation" style={props}>
                {item}
              </animated.div>
            );
          }).props.children
        }
      </Div>
      {getButtonSection()}
    </Div>
  );

  function getButtonSection() {
    if (childrenArray.length <= numberOfItemsPerColumn) return null;
    return (
      <>
        {getLines()}
        {getExpandButton()}
      </>
    );
  }

  function getExpandButton() {
    const buttonText = isExpanded ? buttonContentExpanded : buttonContentClosed;

    return (
      <button
        className={styles.expandButton}
        onClick={onExpandButtonClick}
        data-testid="expand-button"
      >
        <P right="x-small">{buttonText}</P>

        <animated.div style={expandChevronAnimations}>
          <IconPrimary
            icon="chevron-down"
            size="medium"
            className={styles.expandIcon}
          />
        </animated.div>
      </button>
    );
  }

  function getLines() {
    return (
      <Div className={styles.twoLines} data-testid="button-line">
        <Div className={styles.line} />
        <Div className={styles.line} />
      </Div>
    );
  }

  function getVisibleChildren() {
    const visibleChildren = childrenArray.slice(0, numberOfItemsPerColumn);

    return visibleChildren || [];
  }

  function getHiddenChildren() {
    const hiddenChildren = childrenArray.slice(numberOfItemsPerColumn);

    return hiddenChildren || [];
  }

  function onExpandButtonClick() {
    setIsExpanded(!isExpanded);
  }
};

function getExpandChevronAnimation(isExpanded: boolean) {
  return {
    transform: `rotate(${isExpanded ? '180deg' : '0deg'})`,
  };
}

function getCellAnimation() {
  return {
    from: { maxHeight: 0 },
    enter: { maxHeight: 600 },
    leave: { maxHeight: 0 },
    config: { duration: 200 },
  };
}

const StackRows = ({ children, itemsPerColumn }: InnerComponent) => {
  const childrenArray = React.Children.toArray(children);

  const rowsArray = [];

  const rowsArrayLength = Math.ceil(childrenArray.length / itemsPerColumn);
  const leftoverItems = childrenArray.length % itemsPerColumn;

  let childIndex = 0;

  for (let i = 0; i < rowsArrayLength; i++) {
    rowsArray.push(
      childrenArray.slice(childIndex, childIndex + itemsPerColumn)
    );

    childIndex += itemsPerColumn;

    if (i === rowsArrayLength - 1 && leftoverItems > 0) {
      while (rowsArray[i].length < itemsPerColumn) {
        rowsArray[i].push(
          <span
            className={styles.fillerComponent}
            key={rowsArray[i].length}
            data-testid="stack-row-filler"
          />
        );
      }
    }
  }

  const rowClassName = classNames(styles.row, {
    [styles.singleColumn]: itemsPerColumn === 1,
  });

  return (
    <span data-testid="stack-rows-container">
      {rowsArray.map((elements, index) => {
        return (
          <Div
            key={index}
            className={rowClassName}
            data-testid={`stack-row-${index}`}
          >
            {elements}
          </Div>
        );
      })}
    </span>
  );
};

export default ExpandableCardStack;
