import {
  InsuranceDisplayStatus,
  InsuranceProductType,
} from '@pm-netbank/constants';
import { InsuranceAgreement } from '@pm-netbank/types';
import React from 'react';
import { Icon } from '@dnb/eufemia';
import {
  ambulance_medium as ambulance,
  baby_medium as baby,
  bandage_medium as bandage,
  boat_medium as boat,
  building_construction_medium as buildingConstruction,
  car_2_medium as car,
  cat_medium as cat,
  diamond_medium as diamond,
  first_aid_medium as firstAid,
  globe_medium as globe,
  heart_rate_medium as heartRate,
  horse_medium as horse,
  house_1_medium as house,
  house_2_medium as cabin,
  loan_medium as loan,
  motorcycle_medium as motorcycle,
  plant_medium as plant,
  scooter_medium as scooter,
  snowmobile_medium as snowmobile,
  tractor_medium as tractor,
  camper_trailer_medium as trailer,
  vintage_car_medium as vintageCar,
  wheelchair_medium as wheelchair,
} from '@dnb/eufemia/icons';
import SimpleCell from '../SimpleCell/SimpleCell';
import styles from './InsuranceAgreementCell.module.scss';

export interface Props {
  agreement: InsuranceAgreement;
  onClick?: () => void;
  href?: string;
  className?: string;
  children?: React.ReactNode;
  navigationState?: unknown;
}

const InsuranceAgreementCell = ({
  agreement,
  onClick,
  href,
  className,
  children,
  navigationState,
}: Props) => {
  const classNameFromStatus = getClassNameBasedOnDisplayStatus(
    agreement.displayStatus
  );

  return (
    <SimpleCell
      className={`${classNameFromStatus} ${className}`}
      data-testid="insurance-agreement-cell"
      icon={getIcon(agreement.productType)}
      title={agreement.displayProductName}
      subtitle={agreement.displayDescription}
      onClick={onClick}
      href={href}
      navigationState={navigationState}
    >
      {children}
    </SimpleCell>
  );
};

function getClassNameBasedOnDisplayStatus(status: InsuranceDisplayStatus) {
  if (status === InsuranceDisplayStatus.ACCEPT_ONLY) return styles.accepted;

  return '';
}

const iconSettingsByProductType: Record<
  InsuranceProductType,
  { dataTestId: string; icon: React.ReactNode }
> = {
  [InsuranceProductType.ACCIDENT]: {
    dataTestId: 'wheelchair',
    icon: wheelchair,
  },
  [InsuranceProductType.ACCIDENT_COMBINED]: {
    dataTestId: 'wheelchair',
    icon: wheelchair,
  },
  [InsuranceProductType.ACCIDENT_INSURANCE]: {
    dataTestId: 'wheelchair',
    icon: wheelchair,
  },
  [InsuranceProductType.BUILDING_UNDER_CONSTRUCTION]: {
    dataTestId: 'building-construction',
    icon: buildingConstruction,
  },
  [InsuranceProductType.CAR]: { dataTestId: 'car', icon: car },
  [InsuranceProductType.CHILD]: { dataTestId: 'baby', icon: baby },
  [InsuranceProductType.CRITICAL_DISEASE]: {
    dataTestId: 'ambulance',
    icon: ambulance,
  },
  [InsuranceProductType.DEBT_INSURANCE]: { dataTestId: 'loan', icon: loan },
  [InsuranceProductType.DISABILITY_CAPITAL]: {
    dataTestId: 'bandage',
    icon: bandage,
  },
  [InsuranceProductType.DISABILITY_CAPITAL_WITH_CHILD]: {
    dataTestId: 'baby',
    icon: baby,
  },
  [InsuranceProductType.DISABILITY_PENSION]: {
    dataTestId: 'bandage',
    icon: bandage,
  },
  [InsuranceProductType.DISABILITY_PENSION_WITH_CHILD]: {
    dataTestId: 'baby',
    icon: baby,
  },
  [InsuranceProductType.DOG_AND_CAT]: { dataTestId: 'cat', icon: cat },
  [InsuranceProductType.HORSE]: { dataTestId: 'horse', icon: horse },
  [InsuranceProductType.HOUSE]: { dataTestId: 'house', icon: house },
  [InsuranceProductType.HOUSEHOLD_CONTENT]: {
    dataTestId: 'plant',
    icon: plant,
  },
  [InsuranceProductType.LEISURE_BOAT]: { dataTestId: 'boat', icon: boat },
  [InsuranceProductType.LEISURE_HOME]: { dataTestId: 'cabin', icon: cabin },
  [InsuranceProductType.LIFE]: { dataTestId: 'heart-rate', icon: heartRate },
  [InsuranceProductType.MOPED]: { dataTestId: 'scooter', icon: scooter },
  [InsuranceProductType.MOTORBIKE]: {
    dataTestId: 'motorcycle',
    icon: motorcycle,
  },
  [InsuranceProductType.SNOW_SCOOTER]: {
    dataTestId: 'snowmobile',
    icon: snowmobile,
  },
  [InsuranceProductType.TRACTOR]: { dataTestId: 'tractor', icon: tractor },
  [InsuranceProductType.TRAILER_OR_CARAVAN]: {
    dataTestId: 'trailer',
    icon: trailer,
  },
  [InsuranceProductType.TRAVEL]: { dataTestId: 'globe', icon: globe },
  [InsuranceProductType.TREATMENT]: { dataTestId: 'first-aid', icon: firstAid },
  [InsuranceProductType.VALUABLES]: { dataTestId: 'diamond', icon: diamond },
  [InsuranceProductType.VETERAN_CAR]: {
    dataTestId: 'vintage-car',
    icon: vintageCar,
  },
};

function getIcon(productType: InsuranceProductType) {
  const iconSettings = iconSettingsByProductType[productType];

  if (!iconSettings) return null;

  return (
    <Icon
      data-testid={`icon-${iconSettings.dataTestId}`}
      icon={iconSettings.icon}
    />
  );
}

export default InsuranceAgreementCell;
