import React from 'react';
import { Radio } from '@dnb/eufemia/components';
import { ControlProps } from '@jsonforms/core';
import BaseFormRow from '../../layouts/BaseFormRow';

const RadioGroupRenderer = (props: ControlProps) => {
  const { label, handleChange, path, visible, schema, data, errors } = props;
  const values = schema.enum || [];

  return (
    <BaseFormRow visible={visible}>
      <Radio.Group
        data-testid={path || 'radio-group'}
        value={data}
        label_direction="vertical"
        layout_direction="column"
        status={errors}
        label={label}
        on_change={onChange}
      >
        {values.map((value, i) => (
          <Radio key={i} label={value} value={value} top="small" />
        ))}
      </Radio.Group>
    </BaseFormRow>
  );

  function onChange({ value }: { value: string }) {
    return handleChange(path, value);
  }
};
export default RadioGroupRenderer;
