import React from 'react';
import { ControlProps, DispatchPropsOfMultiEnumControl } from '@jsonforms/core';
import BaseToggleButtonGroup from '../BaseToggleButtonGroup';

interface Props extends ControlProps, DispatchPropsOfMultiEnumControl {
  handleChange(path: string, value: string[]): void;
}

const ToggleButtonGroupCheckboxRenderer = (props: Props) => {
  const { path, handleChange } = props;
  return (
    <BaseToggleButtonGroup
      {...props}
      multiselect={true}
      variant="checkbox"
      onChange={(value) => handleChange(path, value.values)}
    />
  );
};

export default ToggleButtonGroupCheckboxRenderer;
