import React from 'react';
import { FormattedMessage } from 'react-intl';
import { VisuallyHidden } from '@dnb/eufemia';
import classnames from 'classnames';
import { translationKeys } from '@pm-netbank/translations';
import styles from './RiskScore.module.scss';

export interface Props {
  value: number;
  showLabel?: boolean;
  showBars?: boolean;
  'data-testid'?: string;
}
const MAXIMUM_RISK_SCORE = 7;

const RiskScore = ({
  value,
  'data-testid': dataTestid,
  showLabel = true,
  showBars,
}: Props) => {
  return (
    <span data-testid={dataTestid || 'risk-score'} className={styles.riskScore}>
      {getLabel()}
    </span>
  );

  function getLabel() {
    return (
      <>
        {showLabel ? (
          getFormattedRiskScore(value)
        ) : (
          <div data-testid="hidden-risk-score-label">
            <VisuallyHidden>{getFormattedRiskScore(value)}</VisuallyHidden>
          </div>
        )}
        {getBars(value)}
      </>
    );
  }

  function getBars(value: number) {
    if (value < 1 || value > MAXIMUM_RISK_SCORE || !showBars) {
      return null;
    }

    const bars = [];
    for (let i = 0; i < MAXIMUM_RISK_SCORE; i++) {
      if (value > i) {
        bars.push(<Bar spaceLeft={showLabel} key={i} type="filled" />);
      } else {
        bars.push(<Bar spaceLeft={showLabel} key={i} type="unfilled" />);
      }
    }
    return bars;
  }

  function getFormattedRiskScore(riskScore: number) {
    const translationValues = {
      riskScore: value,
      maximumRiskScore: MAXIMUM_RISK_SCORE,
    };

    if (riskScore === 1 || riskScore === 2) {
      return (
        <FormattedMessage
          id={translationKeys.components.components_risk_score_low_risk_score}
          values={translationValues}
        />
      );
    }

    if (riskScore === 3 || riskScore === 4) {
      return (
        <FormattedMessage
          id={
            translationKeys.components.components_risk_score_medium_risk_score
          }
          values={translationValues}
        />
      );
    }

    if (riskScore >= 5 && riskScore <= MAXIMUM_RISK_SCORE) {
      return (
        <FormattedMessage
          id={translationKeys.components.components_risk_score_high_risk_score}
          values={translationValues}
        />
      );
    }

    return (
      <FormattedMessage
        id={translationKeys.components.components_risk_score_unknown_risk_score}
      />
    );
  }
};

function Bar({
  type,
  spaceLeft,
}: { type: 'filled' | 'unfilled' } & { spaceLeft?: boolean }) {
  const barStyle = classnames(styles.riskScoreBar, {
    [styles.spaceLeft]: spaceLeft,
  });

  const barRectStyle = classnames({
    [styles.riskScoreBarFilled]: type === 'filled',
    [styles.riskScoreBarUnfilled]: type === 'unfilled',
  });

  return (
    <svg
      className={barStyle}
      data-testid="risk-score-bar-container"
      aria-hidden="true"
      width="8"
      height="20"
      viewBox="0 0 8 20"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <rect
        className={barRectStyle}
        data-testid={`${type}-bar`}
        width="8"
        height="20"
      />
    </svg>
  );
}

export default RiskScore;
