import React from 'react';
import { Div, Icon, IconPrimary, P } from '@dnb/eufemia';
import { launch } from '@dnb/eufemia/icons';
import classNames from 'classnames';
import CellContainer from '../CellContainer/CellContainer';
import styles from './SimpleCell.module.scss';

export interface Props {
  icon?: React.ReactNode;
  header?: string | React.ReactNode;
  title: string | React.ReactNode;
  subtitle?: string | React.ReactNode;
  secondaryText?: string | React.ReactNode;
  secondarySubtitle?: string | React.ReactNode;
  content?: React.ReactNode;
  onClick?: () => void;
  className?: string;
  children?: React.ReactNode;
  href?: string;
  target?: string;
  navigationState?: unknown;
  navigationIcon?: React.ReactNode;
  'data-testid'?: string;
}

const SimpleCell = ({
  icon,
  title,
  header,
  subtitle,
  secondaryText,
  secondarySubtitle,
  content,
  onClick,
  className,
  children,
  href,
  target,
  navigationState,
  navigationIcon,
  'data-testid': dataTestid,
}: Props) => {
  const testId = dataTestid || 'cell';

  const isRightContentEmpty =
    !content && !secondaryText && !secondarySubtitle && !onClick;

  return (
    <CellContainer
      onClick={onClick}
      href={href}
      target={target}
      className={className}
      data-testid={testId}
      navigationState={navigationState}
    >
      <Div className={styles.simpleCell}>
        <Div
          className={classNames(styles.leftContent, {
            [styles.noRightContent]: isRightContentEmpty,
          })}
        >
          {getIcon()}

          <Div className={styles.text}>
            {getHeader()}
            <Div data-testid="cell-title">{title}</Div>
            {getSubtitle()}
          </Div>
        </Div>

        <Div className={styles.rightContent}>
          {getContent()}
          {getSecondaryContent()}
          {getNavigationIcon()}
        </Div>
      </Div>

      {getChildren()}
    </CellContainer>
  );

  function getIcon() {
    if (!icon) return null;

    return (
      <Div className={styles.icon} data-testid="cell-icon">
        {icon}
      </Div>
    );
  }

  function getSubtitle() {
    if (!subtitle) return null;

    return (
      <P size="small" className={styles.subtitle} data-testid="cell-subtitle">
        {subtitle}
      </P>
    );
  }

  function getHeader() {
    if (!header) return null;

    return (
      <P size="small" className={styles.header} data-testid="cell-header">
        {header}
      </P>
    );
  }

  function getContent() {
    if (!content) return null;

    return (
      <Div className={styles.content} data-testid="cell-content">
        {content}
      </Div>
    );
  }

  function getSecondaryContent() {
    if (!secondaryText && !secondarySubtitle) return null;

    return (
      <Div className={styles.secondaryText}>
        {getSecondaryText()}
        {getSecondarySubtitle()}
      </Div>
    );
  }

  function getSecondaryText() {
    if (!secondaryText) return null;

    return <Div data-testid="cell-secondary-text">{secondaryText}</Div>;
  }

  function getSecondarySubtitle() {
    if (!secondarySubtitle) return null;

    return (
      <P
        size="small"
        className={styles.secondarySubtitle}
        data-testid="cell-secondary-subtitle"
      >
        {secondarySubtitle}
      </P>
    );
  }

  function getNavigationIcon() {
    if (!onClick && !href) return null;

    if (navigationIcon) {
      return (
        <Div data-testid="cell-custom-navigation-icon">{navigationIcon}</Div>
      );
    }

    const opensInNewWindow = href && target === '_blank';

    if (opensInNewWindow)
      return (
        <Icon
          data-testid="cell-launcher"
          icon={launch}
          className={styles.launcher}
        />
      );

    return (
      <IconPrimary
        icon="chevron-right"
        data-testid="cell-chevron"
        className={styles.chevron}
      />
    );
  }

  function getChildren() {
    if (!children) return null;
    if (Array.isArray(children) && children.every((child) => child == null))
      return null;

    return <Div data-testid="cell-children">{children}</Div>;
  }
};

export default SimpleCell;
