import React from 'react';
import { Div, Tag } from '@dnb/eufemia';
import SimpleCell from '../Stack/SimpleCell/SimpleCell';
import styles from './FilterDrawer.module.scss';

export interface Props {
  tags?: string[];
  title: string | React.ReactNode;
  href?: string;
  onClick?: () => void;
  onTagDelete?: (index: number) => void;
  children?: React.ReactNode;
}

const Option = ({
  title,
  tags = [],
  onTagDelete,
  children,
  onClick,
  href,
}: Props) => {
  return (
    <Div className={styles.sectionWrapper} data-testid="filter-drawer-option">
      <SimpleCell
        title={title}
        href={href}
        onClick={onClick}
        data-testid="filter-drawer-option-button"
      />
      {getTags()}
      {getChildren()}
    </Div>
  );

  function getTags() {
    if (!tags.length) {
      return null;
    }

    return (
      <Tag.Group
        label={title}
        className={styles.tagGroup}
        data-testid="filter-drawer-option-tag-group"
      >
        {tags.map((tag, index) => (
          <Tag
            key={`${title}-${tag}`}
            data-testid="filter-drawer-option-tag"
            text={tag}
            onDelete={() => onTagDelete && onTagDelete(index)}
          />
        ))}
      </Tag.Group>
    );
  }

  function getChildren() {
    if (!children) {
      return;
    }
    return (
      <Div left="small" right="small" top="x-small" bottom="small">
        {children}
      </Div>
    );
  }
};

export default Option;
