import { getRoutes } from './helpers';

interface SavingRoutes {
  base: () => string;
  savingsAccountSetup: () => string;
}

const enBasePath = '/savings';
const enRoutes: SavingRoutes = {
  base: () => enBasePath,
  savingsAccountSetup: () => `${enBasePath}/accounts/setup`,
};

const noBasePath = '/savings';
const noRoutes: SavingRoutes = {
  base: () => noBasePath,
  savingsAccountSetup: () => `${noBasePath}/accounts/setup`,
};

export default getRoutes(enRoutes, noRoutes);
