import icons from '@dnb/eufemia/icons';
import { Layout, Lableable, Internationalizable } from '@jsonforms/core';

export enum LayoutType {
  Container = 'Container',
  Group = 'Group',
  SinglePageLayout = 'SinglePageLayout',
  HorizontalFormRowLayout = 'HorizontalFormRowLayout',
  Section = 'Section',
  Step = 'Step',
  SteppedLayout = 'SteppedLayout',
}

export interface StepLayout
  extends Layout,
    Lableable<string>,
    Internationalizable {
  type: LayoutType.Step;
}

export interface SectionLayout
  extends Layout,
    Lableable<string>,
    Internationalizable {
  type: LayoutType.Section;
  options?: {
    icon?: keyof typeof icons;
  };
}

export interface SteppedLayout extends Layout {
  type: LayoutType.SteppedLayout;
  options: {
    mode: 'static' | 'strict' | 'loose';
  };
}

export interface ContainerLayout
  extends Layout,
    Lableable<string>,
    Internationalizable {
  type: LayoutType.Container;
  options?: {
    containerType: 'form' | 'stack';
  };
}

export interface GroupLayout extends Layout {
  type: LayoutType.Group;
  options: {
    title: string;
    subTitle?: string;
    i18n?: {
      title: string;
      subTitle?: string;
    };
  };
}

export interface SinglePageLayout extends Layout {
  type: LayoutType.SinglePageLayout;
}

export interface HorizontalFormRowLayout
  extends Layout,
    Lableable<string>,
    Internationalizable {
  type: LayoutType.HorizontalFormRowLayout;
}
