import { getRoutes } from './helpers';

interface SpendingRoutes {
  base: () => string;
  metaCategory: (metaCategoryCode: string) => string;
  mainCategory: (metaCategoryCode: string, mainCategory: string) => string;
  retailerTransactions: (
    metaCategoryCode: string,
    mainCategory: string,
    merchantName: string
  ) => string;
  subCategoryTransactions: (
    metaCategoryCode: string,
    mainCategory: string,
    subCategoryCode: string
  ) => string;
  transactionDetails: (
    metaCategoryCode: string,
    mainCategory: string,
    transactionFilter: string,
    categoryIdentifier: string,
    accountId: string,
    transactionId: string
  ) => string;
  editCategories: () => string;
  uncategorisedTransactionDetails: (
    accountIdentifier: string,
    transactionIdentifier: string
  ) => string;
}

const enBasePath = '/spendings';
const enRoutes: SpendingRoutes = {
  base: () => enBasePath,
  metaCategory: (metaCategoryCode: string) =>
    `${enBasePath}/${metaCategoryCode}`,
  mainCategory: (metaCategoryCode: string, mainCategory: string) =>
    `${enBasePath}/${metaCategoryCode}/${mainCategory}`,
  retailerTransactions: (
    metaCategoryCode: string,
    mainCategory: string,
    merchantName: string
  ) =>
    `${enBasePath}/${metaCategoryCode}/${mainCategory}/retailer/${merchantName}`,
  subCategoryTransactions: (
    metaCategoryCode: string,
    mainCategory: string,
    subCategoryCode: string
  ) =>
    `${enBasePath}/${metaCategoryCode}/${mainCategory}/subcategory/${subCategoryCode}`,
  transactionDetails: (
    metaCategoryCode: string,
    mainCategory: string,
    transactionFilter: string,
    categoryIdentifier: string,
    accountId: string,
    transactionId: string
  ) =>
    `${enBasePath}/${metaCategoryCode}/${mainCategory}/${transactionFilter}/${categoryIdentifier}/${accountId}/${transactionId}`,
  editCategories: () => `${enBasePath}/categories/edit`,
  uncategorisedTransactionDetails: (
    accountIdentifier: string,
    transactionIdentifier: string
  ) =>
    `${enBasePath}/categories/transactions/${accountIdentifier}/${transactionIdentifier}`,
};

const noBasePath = '/spendings';
const noRoutes: SpendingRoutes = {
  base: () => noBasePath,
  metaCategory: (metaCategoryCode: string) =>
    `${noBasePath}/${metaCategoryCode}`,
  mainCategory: (metaCategoryCode: string, mainCategory: string) =>
    `${noBasePath}/${metaCategoryCode}/${mainCategory}`,
  retailerTransactions: (
    metaCategoryCode: string,
    mainCategory: string,
    merchantName: string
  ) =>
    `${noBasePath}/${metaCategoryCode}/${mainCategory}/retailer/${merchantName}`,
  subCategoryTransactions: (
    metaCategoryCode: string,
    mainCategory: string,
    subCategoryCode: string
  ) =>
    `${noBasePath}/${metaCategoryCode}/${mainCategory}/subcategory/${subCategoryCode}`,
  transactionDetails: (
    metaCategoryCode: string,
    mainCategory: string,
    transactionFilter: string,
    categoryIdentifier: string,
    accountId: string,
    transactionId: string
  ) =>
    `${noBasePath}/${metaCategoryCode}/${mainCategory}/${transactionFilter}/${categoryIdentifier}/${accountId}/${transactionId}`,
  editCategories: () => `${noBasePath}/categories/edit`,
  uncategorisedTransactionDetails: (
    accountIdentifier: string,
    transactionIdentifier: string
  ) =>
    `${noBasePath}/categories/transactions/${accountIdentifier}/${transactionIdentifier}`,
};

export default getRoutes(enRoutes, noRoutes);
