import React from 'react';
import { Dropdown } from '@dnb/eufemia/components';
import { ControlProps } from '@jsonforms/core';
import BaseFormRow from '../../layouts/BaseFormRow';

const DropdownRenderer = (props: ControlProps) => {
  const { label, handleChange, path, visible, data, schema, errors } = props;
  const values = schema.enum || [];

  return (
    <BaseFormRow visible={visible}>
      <Dropdown
        data-testid={path || 'dropdown'}
        stretch
        size="medium"
        value={data ? data : null}
        data={values}
        label={label}
        label_direction="vertical"
        status={errors}
        on_change={onChange}
      />
    </BaseFormRow>
  );

  function onChange({ data }: { data: string }) {
    return handleChange(path, data);
  }
};
export default DropdownRenderer;
