import { getRoutes } from './helpers';

interface LoanRoutes {
  base: () => string;
  loanDetails: (loanType: string, accountNumber: string) => string;
  loanDetailsVehicle: () => string;
  loanDetailsInformation: (loanType: string, accountNumber: string) => string;
  loanDetailsTransactions: (loanType: string, accountNumber: string) => string;
  loanDetailsTransactionDetails: (
    loanType: string,
    accountNumber: string,
    transactionId: string
  ) => string;
  loanDetailsRepaymentPlan: (loanType: string, accountNumber: string) => string;
  loanVechicle: (accountNumber: string) => string;
}

const enBasePath = '/loans';
const enRoutes: LoanRoutes = {
  base: () => enBasePath,
  loanDetails: (loanType, accountNumber) =>
    `${enBasePath}/${loanType}/${accountNumber}`,
  loanDetailsVehicle: () => `${enBasePath}/vehicle`,
  loanDetailsInformation: (loanType, accountNumber) =>
    `${enBasePath}/${loanType}/${accountNumber}/information`,
  loanDetailsTransactions: (loanType, accountNumber) =>
    `${enBasePath}/${loanType}/${accountNumber}/transactions`,
  loanDetailsTransactionDetails: (loanType, accountNumber, transactionId) =>
    `${enBasePath}/${loanType}/${accountNumber}/transactions/${transactionId}`,
  loanDetailsRepaymentPlan: (loanType, accountNumber) =>
    `${enBasePath}/${loanType}/${accountNumber}/repayment-plan`,
  loanVechicle: (accountNumber) => `${enBasePath}/vehicle/${accountNumber}`,
};

const noBasePath = '/loans';
const noRoutes: LoanRoutes = {
  base: () => noBasePath,
  loanDetails: (loanType, accountNumber) =>
    `${noBasePath}/${loanType}/${accountNumber}`,
  loanDetailsVehicle: () => `${noBasePath}/vehicle`,
  loanDetailsInformation: (loanType, accountNumber) =>
    `${noBasePath}/${loanType}/${accountNumber}/information`,
  loanDetailsTransactions: (loanType, accountNumber) =>
    `${noBasePath}/${loanType}/${accountNumber}/transactions`,
  loanDetailsTransactionDetails: (loanType, accountNumber, transactionId) =>
    `${enBasePath}/${loanType}/${accountNumber}/transactions/${transactionId}`,
  loanDetailsRepaymentPlan: (loanType, accountNumber) =>
    `${noBasePath}/${loanType}/${accountNumber}/repayment-plan`,
  loanVechicle: (accountNumber) => `${noBasePath}/vehicle/${accountNumber}`,
};

export default getRoutes(enRoutes, noRoutes);
