import { getRoutes } from './helpers';

interface PreQualificationLettersRoutes {
  base: () => string;
  preQualificationLetter: (agreementId: string) => string;
  preQualificationLetterInformation: (agreementId: string) => string;
}

const enBasePath = '/pre-qualification-letters';
const enRoutes: PreQualificationLettersRoutes = {
  base: () => enBasePath,
  preQualificationLetter: (agreementId) => `${enBasePath}/${agreementId}`,
  preQualificationLetterInformation: (agreementId) =>
    `${noBasePath}/${agreementId}/information`,
};

const noBasePath = '/pre-qualification-letters';
const noRoutes: PreQualificationLettersRoutes = {
  base: () => noBasePath,
  preQualificationLetter: (agreementId) => `${noBasePath}/${agreementId}`,
  preQualificationLetterInformation: (agreementId) =>
    `${noBasePath}/${agreementId}/information`,
};

export default getRoutes(enRoutes, noRoutes);
