import classNames from 'classnames';
import styles from './styles.module.scss';
import { SpacingProps } from './types';

export function getSpacingClasses(spacingConfiguration: SpacingProps) {
  const classes = [];

  const classPrefix = 'elementWithSpacing';

  const topValue = spacingConfiguration.space?.top || spacingConfiguration.top;
  if (topValue) {
    const topClassName = `${classPrefix}Top${toCssModuleFormat(topValue)}`;
    classes.push(styles[topClassName]);
  }

  const bottomValue =
    spacingConfiguration.space?.bottom || spacingConfiguration.bottom;
  if (bottomValue) {
    const bottomClassName = `${classPrefix}Bottom${toCssModuleFormat(
      bottomValue
    )}`;
    classes.push(styles[bottomClassName]);
  }

  const leftValue =
    spacingConfiguration.space?.left || spacingConfiguration.left;
  if (leftValue) {
    const leftClassName = `${classPrefix}Left${toCssModuleFormat(leftValue)}`;
    classes.push(styles[leftClassName]);
  }

  const rightValue =
    spacingConfiguration.space?.right || spacingConfiguration.right;
  if (rightValue) {
    const rightClassName = `${classPrefix}Right${toCssModuleFormat(
      rightValue
    )}`;
    classes.push(styles[rightClassName]);
  }

  return classNames(classes);
}

function toCssModuleFormat(value: string) {
  if (!value) return null;

  const parts = value.split('-');

  return parts
    .map((part) => part.charAt(0).toUpperCase() + part.slice(1))
    .join('');
}
