import classnames from 'classnames';
import { Link } from 'gatsby';
import React from 'react';
import styles from './CellContainer.module.scss';

export interface Props {
  className?: string;
  onClick?: () => void;
  href?: string;
  target?: string;
  children?: React.ReactNode;
  style?: React.CSSProperties;
  'data-testid'?: string;
  navigationState?: unknown;
}

const CellContainer = (props: Props) => {
  if (props.href) return <AnchorContainer {...props} />;

  if (props.onClick) return <ButtonContainer {...props} />;

  return <DivContainer {...props} />;
};

function AnchorContainer({
  style,
  className,
  href,
  target,
  children,
  'data-testid': dataTestid,
  navigationState,
}: Props) {
  const cellClassNames = classnames(styles.cell, className, styles.clickable);

  return (
    <Link
      data-testid={dataTestid || 'cell-container'}
      style={style}
      className={cellClassNames}
      to={href}
      target={target}
      state={navigationState}
    >
      {children}
    </Link>
  );
}

function ButtonContainer({
  style,
  className,
  onClick,
  children,
  'data-testid': dataTestid,
}: Props) {
  const cellClassNames = classnames(styles.cell, className, styles.clickable);

  return (
    <button
      data-testid={dataTestid || 'cell-container'}
      style={style}
      className={cellClassNames}
      onClick={onClick}
    >
      {children}
    </button>
  );
}

function DivContainer({
  style,
  className,
  children,
  'data-testid': dataTestid,
}: Props) {
  const cellClassNames = classnames(styles.cell, className);

  return (
    <div
      data-testid={dataTestid || 'cell-container'}
      style={style}
      className={cellClassNames}
    >
      {children}
    </div>
  );
}

export default CellContainer;
