import React from 'react';
import { FormattedMessage } from 'react-intl';
import { Icon } from '@dnb/eufemia';
import { translationKeys } from '@pm-netbank/translations';
import styles from './SocialResponsibilityIndex.module.scss';

export interface Props {
  value: number;
  'data-testid'?: string;
}

enum SocialResponsibilityTypes {
  UNKNOWN = 'unknown',
  RED = 'red',
  YELLOW = 'yellow',
  GREEN = 'green',
}

const SocialResponsibilityIndex = ({
  value,
  'data-testid': dataTestid,
}: Props) => {
  const color: SocialResponsibilityTypes =
    {
      1: SocialResponsibilityTypes.RED,
      2: SocialResponsibilityTypes.YELLOW,
      3: SocialResponsibilityTypes.GREEN,
    }[value] || SocialResponsibilityTypes.UNKNOWN;

  return (
    <div
      data-testid={dataTestid || 'social-responsibility-index'}
      className={styles.sriWrapper}
    >
      <span data-testid={`social-responsibility-icon-${color}`}>
        <CustomIcon color={color} />
      </span>
      <span data-testid="social-responsibility-label">{getLabel()}</span>
    </div>
  );

  function getLabel() {
    switch (color) {
      case SocialResponsibilityTypes.RED:
        return (
          <FormattedMessage
            id={
              translationKeys.components
                .components_social_responsibility_index_red
            }
          />
        );

      case SocialResponsibilityTypes.YELLOW:
        return (
          <FormattedMessage
            id={
              translationKeys.components
                .components_social_responsibility_index_yellow
            }
          />
        );

      case SocialResponsibilityTypes.GREEN:
        return (
          <FormattedMessage
            id={
              translationKeys.components
                .components_social_responsibility_index_green
            }
          />
        );
      default:
        return (
          <FormattedMessage
            id={
              translationKeys.components
                .components_social_responsibility_index_unknown
            }
          />
        );
    }
  }
};

const CustomIcon = ({ color }: { color: string }) => {
  const isUnknownValue = color === SocialResponsibilityTypes.UNKNOWN;

  function leafIcon() {
    return (
      <svg
        width="24"
        height="24"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
        data-testid="social-responsibility-leaf-icon"
      >
        <path
          d="M0 12C0 5.373 5.373 0 12 0s12 5.373 12 12-5.373 12-12 12S0 18.627 0 12z"
          fill="#CCC"
          className={styles[color]}
        />
        <path
          d="M6.75 15.398l1.269-1.1m0 0c.17.355.398.683.678.969a3.487 3.487 0 002.485 1.046c2.95 0 6.837-3.062 5.935-8.425a.238.238 0 00-.254-.2.216.216 0 00-.11.044c-2.952 2.49-9.085-.169-9.085 5.01 0 .544.122 1.075.35 1.555zm0 0c1.082-.939 2.284-1.688 3.606-2.193"
          stroke="#fff"
          strokeWidth="1.5"
          strokeLinecap="round"
          strokeLinejoin="round"
        />
      </svg>
    );
  }

  function questionIcon() {
    return (
      <svg
        width="24"
        height="24"
        viewBox="0 0 24 24"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
        data-testid="social-responsibility-question-icon"
      >
        <circle cx="12" cy="12" r="12" fill="#737373" />
        <path
          d="M10.6584 14.238H12.6384V14.04C12.6384 13.536 12.8364 13.014 13.5744 12.402C14.6904 11.448 16.0404 10.512 16.0404 8.856C16.0404 6.57 14.2944 5.256 11.8284 5.256C9.34444 5.256 8.22844 6.588 7.83244 8.01L9.52444 8.712C9.88444 7.632 10.6764 7.002 11.8284 7.002C13.1064 7.002 13.8984 7.704 13.8984 8.73C13.8984 9.702 13.3404 10.134 12.0984 11.196C11.0724 12.096 10.6584 13.032 10.6584 13.896V14.238ZM11.7024 18.108C12.4404 18.108 13.0704 17.496 13.0704 16.758C13.0704 16.02 12.4404 15.39 11.7024 15.39C10.9644 15.39 10.3524 16.02 10.3524 16.758C10.3524 17.496 10.9644 18.108 11.7024 18.108Z"
          fill="white"
        />
      </svg>
    );
  }

  return (
    <Icon
      icon={isUnknownValue ? questionIcon() : leafIcon()}
      inherit_color={false}
      size="medium"
      space={{ right: 'x-small' }}
    />
  );
};

export default SocialResponsibilityIndex;
