import { getRoutes } from './helpers';

interface OverviewRoute {
  base: () => string;
  reorderAccounts: () => string;
}

const enBasePath = '/overview';
const enRoutes: OverviewRoute = {
  base: () => enBasePath,
  reorderAccounts: () => `${enBasePath}/accounts/reorder`,
};

const noBasePath = '/overview';
const noRoutes: OverviewRoute = {
  base: () => noBasePath,
  reorderAccounts: () => `${noBasePath}/accounts/reorder`,
};

export default getRoutes(enRoutes, noRoutes);
