import React from 'react';
import { Props } from './FilterDrawer.Option';

export type DrawerOptionType = Props & {
  name: string;
  subTitle?: string;
  controller: React.ReactNode;
};

export function findOptionComponentByName(
  childrenArray: React.ReactNode[],
  name: string
): React.ReactElement<DrawerOptionType> | null {
  const filterOption = childrenArray.find((child: React.ReactElement) => {
    return child.props?.controller && child.props?.name === name;
  }) as React.ReactElement<DrawerOptionType>;

  return filterOption || null;
}
