import React from 'react';
import { ToggleButton } from '@dnb/eufemia/components';
import { ControlProps } from '@jsonforms/core';
import BaseFormRow from '../../layouts/BaseFormRow';

interface Props extends ControlProps {
  handleChange(path: string, value: boolean): void;
}

const BooleanSelectorRenderer = (props: Props) => {
  const { handleChange, path, visible, label, data, errors } = props;

  return (
    <BaseFormRow visible={visible}>
      <ToggleButton.Group
        label={label}
        data-testid={path || 'boolean-selector'}
        label_direction="vertical"
        status={errors}
        on_change={onChange}
        layout_direction="row"
        value={data?.toString()}
      >
        <ToggleButton
          data-testid="boolean-selector-option-yes"
          text="Ja"
          value="true"
        />
        <ToggleButton
          data-testid="boolean-selector-option-no"
          text="Nei"
          value="false"
        />
      </ToggleButton.Group>
    </BaseFormRow>
  );

  function onChange({ value }: { value: string }) {
    handleChange(path, value === 'true');
  }
};
export default BooleanSelectorRenderer;
