import { getRoutes } from './helpers';

interface PropertyRoutes {
  base: () => string;
  firstPropertyCalculator: () => string;
  firstPropertyCalculatorAbout: () => string;
}

const enBasePath = '/property';
const enRoutes: PropertyRoutes = {
  base: () => enBasePath,
  firstPropertyCalculator: () => `${enBasePath}/first-property-calculator`,
  firstPropertyCalculatorAbout: () =>
    `${enBasePath}/first-property-calculator/about`,
};

const noBasePath = '/property';
const noRoutes: PropertyRoutes = {
  base: () => noBasePath,
  firstPropertyCalculator: () => `${noBasePath}/first-property-calculator`,
  firstPropertyCalculatorAbout: () =>
    `${noBasePath}/first-property-calculator/about`,
};

export default getRoutes(enRoutes, noRoutes);
