import { getRoutes } from './helpers';

interface InsuranceRoutes {
  base: () => string;
  agreementDetails: (agreementNumber: number) => string;
}

const enBasePath = '/insurance';
const enRoutes: InsuranceRoutes = {
  base: () => enBasePath,
  agreementDetails: (agreementNumber: number) =>
    `${enBasePath}/agreements/${agreementNumber}`,
};

const noBasePath = '/insurance';
const noRoutes: InsuranceRoutes = {
  base: () => noBasePath,
  agreementDetails: (agreementNumber: number) =>
    `${noBasePath}/agreements/${agreementNumber}`,
};

export default getRoutes(enRoutes, noRoutes);
