import React from 'react';
import { RendererProps } from '@jsonforms/core';
import { SinglePageLayout } from '../types';
import { renderChildren } from '../utils';
import BaseLayout from './../BaseLayout';

const SinglePageLayoutRenderer = ({
  schema,
  uischema,
  enabled,
  visible,
  path,
  renderers,
}: RendererProps) => {
  const layout = uischema as SinglePageLayout;

  if (!visible) return null;

  return (
    <BaseLayout>
      {renderChildren(layout, schema, path, enabled, renderers)}
    </BaseLayout>
  );
};

export default React.memo(SinglePageLayoutRenderer);
