import React, { ComponentPropsWithoutRef } from 'react';
import { DatePicker } from '@dnb/eufemia';
import { startOfYear, subYears, subMonths, endOfYear } from 'date-fns';
import { translationKeys } from '@pm-netbank/translations';
import FormattedMessageWithTestId from '../FormattedMessageWithTestId/FormattedMessageWithTestId';

export type Props = ComponentPropsWithoutRef<typeof DatePicker>;

const DateRangePickerInput = (props: Props) => (
  <DatePicker
    stretch
    data-testid="date-range-picker"
    label={
      <FormattedMessageWithTestId
        data-testid="date-range-picker-label"
        id={translationKeys.components.components_date_range_picker_label}
      />
    }
    start_date={startOfYear(new Date())}
    end_date={new Date()}
    size="medium"
    bottom="small"
    label_direction="vertical"
    range={true}
    show_input={true}
    shortcuts={getDatePickerShortcuts()}
    {...props}
  />
);

function getDatePickerShortcuts() {
  return [
    {
      title: (
        <FormattedMessageWithTestId
          data-testid="date-range-picker-last-six-months-button-label"
          id={
            translationKeys.components
              .components_date_range_picker_last_six_months_button_label
          }
        />
      ),
      start_date: subMonths(new Date(), 6),
      end_date: new Date(),
      close_on_select: true,
    },
    {
      title: (
        <FormattedMessageWithTestId
          data-testid="date-range-picker-this-year-button-label"
          id={
            translationKeys.components
              .components_date_range_picker_last_this_year_button_label
          }
        />
      ),
      start_date: startOfYear(new Date()),
      end_date: new Date(),
      close_on_select: true,
    },
    {
      title: (
        <FormattedMessageWithTestId
          data-testid="date-range-picker-last-year-button-label"
          id={
            translationKeys.components
              .components_date_range_picker_last_last_year_button_label
          }
        />
      ),
      start_date: startOfYear(subYears(new Date(), 1)),
      end_date: endOfYear(subYears(new Date(), 1)),
      close_on_select: true,
    },
  ];
}

export default DateRangePickerInput;
