import { getRoutes } from './helpers';

interface PaymentsRoute {
  base: () => string;
  paymentsMine: () => string;
  paymentsOther: (customerId: string) => string;
  pendingPaymentDetails: (paymentId: string) => string;
  invoiceDetailsMine: (einvoiceId: string) => string;
  invoiceDetailsOther: (customerId: string, einvoiceId: string) => string;
}

const enBasePath = '/payments';
const enRoutes: PaymentsRoute = {
  base: () => enBasePath,
  paymentsMine: () => `${enBasePath}/mine`,
  paymentsOther: (customerId) => `${enBasePath}/other/${customerId}`,
  pendingPaymentDetails: (paymentId) => `${enBasePath}/mine/${paymentId}`,
  invoiceDetailsMine: (einvoiceId) =>
    `${enBasePath}/mine/einvoice/${einvoiceId}`,
  invoiceDetailsOther: (customerId, einvoiceId) =>
    `${enBasePath}/other/${customerId}/einvoice/${einvoiceId}`,
};

const noBasePath = '/payments';
const noRoutes: PaymentsRoute = {
  base: () => noBasePath,
  paymentsMine: () => `${noBasePath}/mine`,
  paymentsOther: (customerId) => `${enBasePath}/other/${customerId}`,
  pendingPaymentDetails: (paymentId) => `${noBasePath}/mine/${paymentId}`,
  invoiceDetailsMine: (einvocieId) =>
    `${noBasePath}/mine/einvoice/${einvocieId}`,
  invoiceDetailsOther: (customerId, einvoiceId) =>
    `${enBasePath}/other/${customerId}/einvoice/${einvoiceId}`,
};

export default getRoutes(enRoutes, noRoutes);
