import classNames from 'classnames';
import React from 'react';
import { Skeleton } from '@dnb/eufemia';
import SkeletonCell from './SkeletonCell/SkeletonCell';
import styles from './Stack.module.scss';

interface Props {
  isSkeleton?: boolean;
  className?: string;
  children?: React.ReactNode;
  numberOfSkeletonItems?: number;
  'data-testid'?: string;
}

const Stack = ({
  isSkeleton,
  className,
  children,
  numberOfSkeletonItems = 3,
  'data-testid': dataTestid,
}: Props) => {
  const classes = classNames(styles.stack, className, {});
  const allCells = isSkeleton
    ? new Array(numberOfSkeletonItems).fill(<SkeletonCell />)
    : React.Children.toArray(children);
  const testId = dataTestid || 'stack';

  return (
    <Skeleton show={isSkeleton}>
      <ul
        className={classes}
        data-testid={isSkeleton ? 'skeleton-stack' : testId}
      >
        {allCells.map((child, index) => (
          <li key={index}>{child}</li>
        ))}
      </ul>
    </Skeleton>
  );
};

export default Stack;
