import React, { useMemo } from 'react';
import { Autocomplete, Span, NumberFormat } from '@dnb/eufemia';
import { format } from '@dnb/eufemia/components/number-format/NumberUtils';
import { AccountInfo } from '@pm-netbank/types';

export interface Props {
  accounts: AccountInfo[];
  onChange: (value: string) => void;
  errorMessage?: string;
  infoMessage?: string;
  onBlur?: React.FocusEventHandler<HTMLElement>;
  value?: string;
  label?: string | React.ReactNode;
  disabled?: boolean;
  placeholder?: string;
  'data-testid'?: string;
}

interface AccountSelectorValue {
  selected_key: string;
  selected_value: string;
  content: React.ReactNode[];
  suffix_value: React.ReactNode;
}

const AccountSelector = ({
  accounts,
  onChange,
  errorMessage,
  infoMessage,
  onBlur,
  value,
  label,
  placeholder,
  disabled,
  'data-testid': dataTestid,
}: Props) => {
  const formattedAccounts = useMemo(
    () => getFormattedAccounts(accounts),
    [accounts]
  );

  return (
    <Autocomplete
      data-testid={dataTestid || 'account-selector'}
      label={label}
      value={getSelectedAccountIndex(accounts, value)}
      data={formattedAccounts}
      placeholder={placeholder}
      on_change={onInputChange}
      on_blur={onBlur}
      disabled={disabled}
      status={errorMessage ? getErrorMessage() : getInfoMessage()}
      status_state={errorMessage ? 'error' : 'info'}
      size="medium"
      show_submit_button
      skip_portal
      stretch
      status_props={{ stretch: true }}
    />
  );

  function onInputChange({ data }: { data: AccountSelectorValue }) {
    onChange(data?.selected_key);
  }

  function getErrorMessage() {
    if (!errorMessage) return null;

    return <Span data-testid="account-selector-error">{errorMessage}</Span>;
  }

  function getInfoMessage() {
    if (!infoMessage) return null;

    return <Span data-testid="account-selector-info">{infoMessage}</Span>;
  }

  function getFormattedAccounts(
    accounts: AccountInfo[]
  ): AccountSelectorValue[] {
    return accounts?.map((account) => {
      const formattedAccountNumber = format(account.accountNumber, {
        ban: true,
      });

      const balance = account.balance;
      const displayName = account.displayName;

      const suffix_value = balance ? (
        <NumberFormat value={balance.value} currency={balance.currency} />
      ) : null;

      const selectedValue = displayName
        ? `${displayName} (${formattedAccountNumber})`
        : `${formattedAccountNumber}`;

      return {
        selected_key: account.accountNumber,
        selected_value: selectedValue,
        suffix_value,
        content: getSelectorValueContent(
          formattedAccountNumber.toString(),
          displayName,
          account.ownerName,
          account.owner
        ),
      };
    });
  }
};

function getSelectorValueContent(
  accountNumber: string,
  displayName: string,
  ownerName: string,
  isOwner: boolean
): string[] {
  if (isOwner) return [displayName, accountNumber];

  return [displayName, ownerName, accountNumber];
}

function getSelectedAccountIndex(
  accounts: AccountInfo[],
  value: string
): number {
  if (!value) return null;

  return accounts?.findIndex((account) => account.accountNumber === value);
}

export default AccountSelector;
