import { getRoutes } from './helpers';

interface TransfersRoutes {
  base: () => string;
  recurringTransfers: () => string;
}

const enBasePath = '/transfers';
const enRoutes: TransfersRoutes = {
  base: () => enBasePath,
  recurringTransfers: () => `${enBasePath}/recurring`,
};

const noBasePath = '/transfers';
const noRoutes: TransfersRoutes = {
  base: () => noBasePath,
  recurringTransfers: () => `${noBasePath}/recurring`,
};

export default getRoutes(enRoutes, noRoutes);
