import React, { ReactElement, useState } from 'react';
import { useSpring, animated, useTransition } from 'react-spring';
import { IconPrimary } from '@dnb/eufemia';
import Stack from '../Stack';
import styles from './ExpandableStack.module.scss';

interface Props {
  children: React.ReactNode;
  isSkeleton?: boolean;
  className?: string;
  'data-testid'?: string;
}

interface InnerComponent {
  children: React.ReactNode;
}

const ExpandableStack = ({
  isSkeleton,
  className,
  children,
  'data-testid': dataTestid,
}: Props) => {
  const [isExpanded, setIsExpanded] = useState(false);
  const childrenArray = React.Children.toArray(children);
  const visible = getVisibleChildren();
  const hidden = getHiddenChildren();

  const expandChevronAnimations = useSpring(
    getExpandChevronAnimation(isExpanded)
  );
  const translateDecoration = useSpring(getDecorationAnimation(isExpanded));
  const expandCellTransition = useTransition(
    isExpanded ? hidden : [],
    getCellAnimation()
  );

  return (
    <div
      className={styles.stack}
      data-testid={dataTestid || 'expandable-stack'}
    >
      <Stack isSkeleton={isSkeleton} className={className}>
        {visible}
        {
          expandCellTransition((props, item) => {
            return <animated.div style={props}>{item}</animated.div>;
          }).props.children
        }
      </Stack>

      {getDecorationView()}
      {getExpandButton()}
    </div>
  );

  function getExpandButton() {
    if (hidden.length === 0) return null;

    return (
      <button
        className={styles.expandButton}
        onClick={onExpandButtonClick}
        data-testid="expand-button"
      >
        <animated.div style={expandChevronAnimations}>
          <IconPrimary
            icon={'chevron-down'}
            size="medium"
            className={styles.expandIcon}
          />
        </animated.div>
      </button>
    );
  }

  function getDecorationView() {
    if (hidden.length === 0 || isExpanded) return null;

    return (
      <animated.div style={{ ...translateDecoration }}>
        <div className={styles.decoration} data-testid="stack-decoration"></div>
      </animated.div>
    );
  }

  function getVisibleChildren() {
    const visible = childrenArray.find(isVisibleChild);

    if (!visible) return [];

    return extractContainerChildren(visible) || [];
  }

  function getHiddenChildren() {
    const hidden = childrenArray.find(isHiddenChild);

    if (!hidden) return [];

    return extractContainerChildren(hidden) || [];
  }

  function onExpandButtonClick() {
    setIsExpanded(!isExpanded);
  }

  function getCellAnimation() {
    const expandAnimationSpeed = getExpandAnimationSpeed();

    return {
      initial: { maxHeight: 0 },
      from: { maxHeight: 0 },
      enter: { maxHeight: 200 },
      leave: { maxHeight: 0 },
      config: { duration: expandAnimationSpeed },
    };
  }

  /**
   * The minimum animation speed should be 200ms, if there are more cells the
   * animation should be 50ms longer per cell.
   */
  function getExpandAnimationSpeed(): number {
    const minAnimationSpeed = 200;
    if (hidden.length < 4) return minAnimationSpeed;

    const additionalSpeed = (hidden.length - 4) * 50;

    return minAnimationSpeed + additionalSpeed;
  }
};

function isVisibleChild(child: ReactElement) {
  return child.type === ExpandableStack.Visible;
}

function isHiddenChild(child: ReactElement) {
  return child.type === ExpandableStack.Hidden;
}

function extractContainerChildren(child: React.ReactNode) {
  return React.Children.toArray((child as ReactElement).props.children);
}

function getExpandChevronAnimation(isExpanded: boolean) {
  return {
    transform: `rotate(${isExpanded ? '180deg' : '0deg'})`,
  };
}

function getDecorationAnimation(isExpanded: boolean) {
  return {
    transform: `translateY(${isExpanded ? '-16px' : '0px'})`,
    opacity: isExpanded ? 0 : 1,
  };
}

ExpandableStack.Visible = ({ children }: InnerComponent) => {
  return <React.Fragment key="children">{children}</React.Fragment>;
};

ExpandableStack.Hidden = ({ children }: InnerComponent) => {
  return <React.Fragment key="children">{children}</React.Fragment>;
};

export default ExpandableStack;
