import React from 'react';
import { FormattedMessage } from 'react-intl';
import { SimpleCell } from '@pm-netbank/components';
import { TransactionDisplayType } from '@pm-netbank/constants';
import { translationKeys } from '@pm-netbank/translations';
import SecondaryTextWrapper from '../SecondaryTextWrapper/SecondaryTextWrapper';
import { getTransactionTypeTextId } from './transactionsTypeText';

export interface Props {
  type: TransactionDisplayType;
}

const TransactionTypeCell = ({ type }: Props) => {
  return (
    <SimpleCell
      data-testid="transaction-type-cell"
      title={
        <FormattedMessage
          id={
            translationKeys.components.components_transaction_details_type_text
          }
        />
      }
      secondaryText={
        <SecondaryTextWrapper>
          <FormattedMessage id={getTransactionTypeTextId(type)} />
        </SecondaryTextWrapper>
      }
    />
  );
};

export default TransactionTypeCell;
