import { getRoutes } from './helpers';

interface PensionRoutes {
  base: () => string;
  pensionAccount: (agreementId: string) => string;
  pensionAccountInformation: (agreementId: string) => string;
  pensionAccountExtendedProfileInformation: (agreementId: string) => string;
  pensionChangeProfile: (agreementId: string) => string;
  pensionAccountFlex: (agreementId: string) => string;
  pensionAccountPkb: (agreementId: string) => string;
  pensionAccountPkbSecured: (agreementId: string) => string;
  pensionAccountIndividual: (agreementId: string) => string;
}

const enBasePath = '/pension';
const enRoutes: PensionRoutes = {
  base: () => enBasePath,
  pensionAccount: (agreementId: string) =>
    `${enBasePath}/pension-account/${agreementId}`,
  pensionAccountInformation: (agreementId: string) =>
    `${enBasePath}/pension-account/${agreementId}/account-information`,
  pensionAccountExtendedProfileInformation: (agreementId: string) =>
    `${enBasePath}/pension-account/${agreementId}/extended-profile-information`,
  pensionChangeProfile: (agreementId: string) =>
    `${enBasePath}/change-profile/${agreementId}`,
  pensionAccountFlex: (agreementId: string) =>
    `${enBasePath}/pension-account-flex/${agreementId}`,
  pensionAccountPkb: (agreementId: string) =>
    `${enBasePath}/pension-capital-certificate/${agreementId}`,
  pensionAccountPkbSecured: (agreementId: string) =>
    `${enBasePath}/pension-capital-certificate-secure/${agreementId}`,
  pensionAccountIndividual: (agreementId: string) =>
    `${enBasePath}/individual-pension-account/${agreementId}`,
};

const noBasePath = '/pension';
const noRoutes: PensionRoutes = {
  base: () => noBasePath,
  pensionAccount: (agreementId: string) =>
    `${noBasePath}/pension-account/${agreementId}`,
  pensionAccountInformation: (agreementId: string) =>
    `${noBasePath}/pension-account/${agreementId}/account-information`,
  pensionAccountExtendedProfileInformation: (agreementId: string) =>
    `${noBasePath}/pension-account/${agreementId}/extended-profile-information`,
  pensionChangeProfile: (agreementId: string) =>
    `${noBasePath}/change-profile/${agreementId}`,
  pensionAccountFlex: (agreementId: string) =>
    `${noBasePath}/pension-account-flex/${agreementId}`,
  pensionAccountPkb: (agreementId: string) =>
    `${noBasePath}/pension-capital-certificate/${agreementId}`,
  pensionAccountPkbSecured: (agreementId: string) =>
    `${noBasePath}/pension-capital-certificate-secure/${agreementId}`,
  pensionAccountIndividual: (agreementId: string) =>
    `${noBasePath}/individual-pension-account/${agreementId}`,
};

export default getRoutes(enRoutes, noRoutes);
